// Цей файл використовується для об'єднання (підключення) всіх інших файлів зі стилями Вашої частини проєкту
.container-accessories {
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    padding-left: 80px;
    padding-right: 80px;
  }
}

.accessories {
  @media screen and (min-width: $desktop-min) {
    background-image: url(../images/img-bg/bg3-desktop.jpg);
    background-size: contain;
    background-repeat: no-repeat;
  }
}

.accessories__accessories-buttons-list {
   display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 25px;
    margin-bottom: 40px; 
    @media screen and (max-width: 767px) {
      width: 280px;
      margin-right: auto;
      margin-left: auto;
    }
    @media screen and (min-width: 768px) {
        gap:53px;
        margin-bottom: 72px;
    }
    @media screen and (min-width: 1280px) {
        gap: 44px;
    }
}

.accessories__title {
  @media screen and (min-width: 481px) {
    padding-left: 145px;
  }
  @media screen and (min-width: 769px) {
    padding-left: 0;
  }
}


.accessories-products-list {
    text-align: -webkit-center;
   margin-top: 16px;
   @media  screen and(min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 30px;
    margin-top: 24px;
   }
   @media screen and(min-width: 1280px) {
    gap: 20px;
   }


}
.accessories-products-list__wrap {
    position: relative;
    overflow: hidden;
    max-width: 285px;
    margin-bottom: 8px;
    border-radius: 12px;
    border: 1px solid $green-text-color;
    border-radius: 12px;
    box-shadow: 0px 4px 4px rgba(134, 229, 67, 0.2);
}
.accessories-products-list__text {
position: absolute;
top:0;
left:0;
width: 100%;
height: 100%;
padding-left: 16px;
padding-right: 16px;
padding-top: 16px;
padding-bottom: 52px;
text-align: left;
font-size: 14px;
line-height: 1.14;
background-color: $teal-text-color;
color: $white-text-color;
transform: translateY(100%);
transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
.accessories-products-list__item:hover &,
.accessories-products-list__item:focus & {
    transform: translateY(0);
  }
}

.accessories-products-list__text-wrap {
  display: inline-block;
  margin-bottom: 15px;
}
.box-prices {

    display: flex;
    gap: 80px;
    align-items: center;
    padding-left: 8px;
    padding-bottom: 8px;
}

.wrap-contents {
    max-width: 285px;
    text-align: left;
}
.wrap-contents__title {
  padding-left: 8px;
  padding-right: 16px;
  margin-bottom: 8px;
  font-weight: 500;
  font-size: 22px;
  line-height: 1.18;
  color: $green-text-color;
}

.accessories-products-list__link {
  display: inline-block;
}
.accessories-products-list__item {
  
     @media screen and(min-width: 1280px) {
        flex-basis: calc((100% - 60px) / 4) ;
    }
}
.accessories-products-list__item:nth-child(n + 7) {
  @media screen and (max-width: 767px) {
    display: none;
  }
}
.accessories-products-list__item:nth-child(n + 13) {
  @media screen and (max-width: 1279px) {
    display: none;
  }
}
.accessories-products-list__item:not(:last-child) {
    margin-bottom: 30px;
    @media screen and (min-width: 768px) {
        margin-bottom: 0;
    }
    }

.preloader {
  display: flex;
  text-align: center;
  justify-content: center;
  gap: 16px;
  padding-top: 40px;
}

.preloader__ellipse {
  width: 40px;
  height: 16px;
  border-radius: 30px;
  background-color: $white-text-color;
  animation: preloader-scale 2000ms linear infinite;
}

.preloader__circle {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: $white-text-color;
  animation-name: preloader-opacity;
  animation-duration: 2000ms; 
  animation-timing-function: linear;
  animation-iteration-count: infinite;
} 

@keyframes preloader-scale {
  100% {
    transform: scaleX(1.2);
  }
}
@keyframes preloader-opacity {
  100% {
    opacity: 0;
  }
}

.back-to-top {
  position: fixed;
  right: 10px;
  bottom: 100px;
  width: 50px;
  height: 50px;
  border-color: transparent;
  border-radius: 50px;
  background-color: $green-text-color;
  transition: filter 250ms cubic-bezier(0.4, 0, 0.2, 1); ;
  &:hover, &:focus {
    filter: brightness(1.5);  
  }
  &::before, &::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  &::before {
    width: 5px;
    height: 25px;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    background-color: $dark-text-color;
  }
  &::after {
    width: 19px;
    height: 19px;
    transform: translate(-50%, -50%) rotate(45deg);
    border-top: 5px solid $dark-text-color;
    border-left: 5px solid $dark-text-color;
  }
} 



